.cursor {
    display: none;
    width: 20px;
    height: 20px;
    position: fixed;
    border: 2px solid var(--secondary);
    border-radius: 50%;
    transform: translate(-50%, -50%);
    pointer-events: none;
    animation: cursorAnime 0.5s infinite alternate;
    z-index: 99;
}

.cursor::after {
    content: "";
    position: absolute;
    width: 20px;
    height: 20px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: 8px solid var(--primary);
    border-radius: 50%;
    opacity: 0.5;
}

.cursor.clicked {
    animation: cursorAnime3 0.25s infinite alternate;
}

@keyframes cursorAnime {
    from {
        transform: translate(-50%, -50%) scale(1);
    }
    to {
        transform: translate(-50%, -50%) scale(0.75);
    }
}

@keyframes cursorAnime2 {
    from {
        transform: translate(-50%, -50%) scale(1);
    }
    to {
        transform: translate(-50%, -50%) scale(0.5);
    }
}

@keyframes cursorAnime3 {
    from {
        transform: translate(-50%, -50%) scale(1);
    }
    to {
        transform: translate(-50%, -50%) scale(1.5);
    }
}
