@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab&family=Rosarivo:ital@0;1&display=swap);
:root {
    --primary: #420824;
    --secondary: #721240;
    --tertiary: #0e081a;
    --quaternary: #ffeeec;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: "Rosarivo", serif;
}

#root {
    background-color: #0e081a;
    background-color: var(--tertiary);
    width: auto;
    height: auto;
}

body::-webkit-scrollbar {
    width: 0;
}

header {
    height: 80px;
    text-transform: uppercase;
    text-decoration: none;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

main {
    min-height: 100vh;
    min-width: 100vw;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

footer {
    height: 80px;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

.particles {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.home {
    background: transparent;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100%;
    width: 100%;
}

.eternity {
    fill: transparent;
    -webkit-animation: fill-line 0.5s ease-in-out forwards 2s;
            animation: fill-line 0.5s ease-in-out forwards 2s;
}

.eternity path {
    stroke: var(--tertiary);
    stroke-width: 0.4px;
    -webkit-animation: line-anim 1.5s ease-in-out forwards;
            animation: line-anim 1.5s ease-in-out forwards;
}

.eternity path:nth-child(1) {
    stroke-dasharray: 60;
    stroke-dashoffset: 60;
    -webkit-animation-delay: .1s;
            animation-delay: .1s;
}
.eternity path:nth-child(2) {
    stroke-dasharray: 45;
    stroke-dashoffset: 45;
    -webkit-animation-delay: .2s;
            animation-delay: .2s;
}
.eternity path:nth-child(3) {
    stroke-dasharray: 56;
    stroke-dashoffset: 56;
    -webkit-animation-delay: .3s;
            animation-delay: .3s;
}
.eternity path:nth-child(4) {
    stroke-dasharray: 57;
    stroke-dashoffset: 57;
    -webkit-animation-delay: .4s;
            animation-delay: .4s;
}
.eternity path:nth-child(5) {
    stroke-dasharray: 65;
    stroke-dashoffset: 65;
    -webkit-animation-delay: .5s;
            animation-delay: .5s;
}
.eternity path:nth-child(6) {
    stroke-dasharray: 30;
    stroke-dashoffset: 30;
    -webkit-animation-delay: .6s;
            animation-delay: .6s;
}
.eternity path:nth-child(7) {
    stroke-dasharray: 45;
    stroke-dashoffset: 45;
    -webkit-animation-delay: .7s;
            animation-delay: .7s;
}
.eternity path:nth-child(8) {
    stroke-dasharray: 45;
    stroke-dashoffset: 45;
    -webkit-animation-delay: .8s;
            animation-delay: .8s;
}

@-webkit-keyframes line-anim {
    to {
        stroke-dashoffset: 0;
    }
}

@keyframes line-anim {
    to {
        stroke-dashoffset: 0;
    }
}

@-webkit-keyframes fill-line {
    to {
        fill: var(--tertiary);
        -webkit-filter: drop-shadow(8px 8px 8px var(--primary));
                filter: drop-shadow(8px 8px 8px var(--primary));
    }
}

@keyframes fill-line {
    to {
        fill: var(--tertiary);
        -webkit-filter: drop-shadow(8px 8px 8px var(--primary));
                filter: drop-shadow(8px 8px 8px var(--primary));
    }
}

.electro {
    fill: none;
    transition: 3s ease;
    -webkit-animation: rotateSvg 8s linear infinite;
            animation: rotateSvg 8s linear infinite;
}

.electro path {
    fill: var(--tertiary);
    -webkit-filter: drop-shadow(0 0 3px var(--primary));
            filter: drop-shadow(0 0 3px var(--primary));
}

.electro circle {
    fill: var(--secondary);
    -webkit-filter: blur(8px);
            filter: blur(8px);
}

@-webkit-keyframes rotateSvg {
    to {
        -webkit-transform: rotate(2turn);
                transform: rotate(2turn);
    }
}

@keyframes rotateSvg {
    to {
        -webkit-transform: rotate(2turn);
                transform: rotate(2turn);
    }
}
.cursor {
    display: none;
    width: 20px;
    height: 20px;
    position: fixed;
    border: 2px solid var(--secondary);
    border-radius: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    pointer-events: none;
    -webkit-animation: cursorAnime 0.5s infinite alternate;
            animation: cursorAnime 0.5s infinite alternate;
    z-index: 99;
}

.cursor::after {
    content: "";
    position: absolute;
    width: 20px;
    height: 20px;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    border: 8px solid var(--primary);
    border-radius: 50%;
    opacity: 0.5;
}

.cursor.clicked {
    -webkit-animation: cursorAnime3 0.25s infinite alternate;
            animation: cursorAnime3 0.25s infinite alternate;
}

@-webkit-keyframes cursorAnime {
    from {
        -webkit-transform: translate(-50%, -50%) scale(1);
                transform: translate(-50%, -50%) scale(1);
    }
    to {
        -webkit-transform: translate(-50%, -50%) scale(0.75);
                transform: translate(-50%, -50%) scale(0.75);
    }
}

@keyframes cursorAnime {
    from {
        -webkit-transform: translate(-50%, -50%) scale(1);
                transform: translate(-50%, -50%) scale(1);
    }
    to {
        -webkit-transform: translate(-50%, -50%) scale(0.75);
                transform: translate(-50%, -50%) scale(0.75);
    }
}

@-webkit-keyframes cursorAnime2 {
    from {
        -webkit-transform: translate(-50%, -50%) scale(1);
                transform: translate(-50%, -50%) scale(1);
    }
    to {
        -webkit-transform: translate(-50%, -50%) scale(0.5);
                transform: translate(-50%, -50%) scale(0.5);
    }
}

@keyframes cursorAnime2 {
    from {
        -webkit-transform: translate(-50%, -50%) scale(1);
                transform: translate(-50%, -50%) scale(1);
    }
    to {
        -webkit-transform: translate(-50%, -50%) scale(0.5);
                transform: translate(-50%, -50%) scale(0.5);
    }
}

@-webkit-keyframes cursorAnime3 {
    from {
        -webkit-transform: translate(-50%, -50%) scale(1);
                transform: translate(-50%, -50%) scale(1);
    }
    to {
        -webkit-transform: translate(-50%, -50%) scale(1.5);
                transform: translate(-50%, -50%) scale(1.5);
    }
}

@keyframes cursorAnime3 {
    from {
        -webkit-transform: translate(-50%, -50%) scale(1);
                transform: translate(-50%, -50%) scale(1);
    }
    to {
        -webkit-transform: translate(-50%, -50%) scale(1.5);
                transform: translate(-50%, -50%) scale(1.5);
    }
}

nav {
    position: fixed;
    top: 0;
    z-index: 9;
    height: 80px;
    width: 100%;
}

.navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: var(--quaternary);
}

.navbar-container {
    display: flex;
    align-items: center;
    justify-self: flex-start;
    height: 100%;
    max-width: 1500px;
    margin: 0 20px;
}

.navbar-logo {
    display: flex;
    align-items: center;
    justify-self: center;
    text-decoration: none;
    margin: 0 10px;
    cursor: pointer;
    color: var(--tertiary);
}

.electro {
    width: 70px;
}

.eternity {
    width: 16rem;
    height: 60px;
}

.menu-icon {
    display: none;
}

.nav-menu {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin: 0 20px;
    font-size: 1rem;
}

.nav-menu button {
    color: var(--tertiary);
    border-color: var(--tertiary);
    background-color: var(--quaternary);
}

.nav-list {
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-gap: 20px;
    list-style: none;
    text-align: center;
}

.nav-item {
    position: relative;
    display: flex;
    width: 100px;
    height: 40px;
}

.nav-item-mobile {
    display: none;
}

.nav-link {
    color: var(--tertiary);
    width: 100%;
    height: 100%;
    padding: 10px 20px;
    position: relative;
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    overflow: hidden;
    transition: .4s ease-in-out;
}

.nav-link::before,
.nav-link::after {
    content: "";
    position: absolute;
    box-sizing: border-box;
    transition: 0.4s ease-in-out;
    z-index: -1;
}

.nav-link::before {
    background: var(--secondary);
    width: 125%;
    height: 50%;
    -webkit-animation: menuItems 4s linear infinite;
            animation: menuItems 4s linear infinite;
}

.nav-link::after {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    -webkit-transform: scale(0.95, 0.9);
            transform: scale(0.95, 0.9);
    background: var(--quaternary);
}

@-webkit-keyframes menuItems {
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes menuItems {
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.nav-link:hover::before {
    height: 300%;
    width: 300%;
}

.nav-link:hover::after {
    background: var(--secondary);
}

.nav-link:hover {
    box-shadow: 6px 6px 6px var(--primary);
    color: var(--quaternary);
}

@media screen and (max-width: 960px) {
    .navbar-container {
        display: flex;
        justify-content: space-between;
        width: 100%;
    }

    .menu-icon {
        display: flex;
        cursor: pointer;
        transition: .6s ease-out;
    }

    .menu-icon svg {
        stroke: var(--tertiary);
        width: 40px;
        height: 40px;
    }

    .menu-icon.active svg {
        stroke: var(--secondary);
    }

    .nav-menu .btn {
        display: none;
    }

    .nav-item,
    .nav-item-mobile {
        position: relative;
        display: flex;
        width: auto;
        height: 50px;
    }

    .nav-list {
        margin: 0;
        padding: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 20%;
        flex-direction: column;
    }

    .nav-menu {
        width: 100%;
        height: 90vh;
        margin: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 80px;
        right: -100%;
        opacity: 0;
        transition: all 0.5s ease;
        background: repeating-linear-gradient(45deg, var(--secondary), var(--primary));
    }

    .nav-menu.active {
        right: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }

    .nav-link {
        text-align: center;
        align-items: center;
        justify-content: flex-start;
        padding: 16px;
        width: 100%;
        display: flex;
        transition: all 0.4s ease;
        background: var(--quaternary);
        box-shadow: 6px 6px 6px var(--tertiary);
    }

    .nav-link::after,
    .nav-link::before {
        display: none;
    }

    .nav-link:hover {
        background-color: var(--tertiary);
        color: var(--quaternary);
        -webkit-transform: scale(1.2);
                transform: scale(1.2);
        -webkit-animation: vibrate 0.5s ease-in;
                animation: vibrate 0.5s ease-in;
        box-shadow: 6px 6px 6px var(--tertiary);
    }

    @-webkit-keyframes vibrate {
        0% {
            -webkit-transform: translate(0);
                    transform: translate(0);
        }
        20%,
        40%,
        60%,
        80% {
            -webkit-transform: translate(0.4rem, 0.2rem);
                    transform: translate(0.4rem, 0.2rem);
        }
        10%,
        30%,
        50%,
        90% {
            -webkit-transform: translate(-0.2rem, -0.1rem);
                    transform: translate(-0.2rem, -0.1rem);
        }
        70%,
        100% {
            -webkit-transform: translateY(0);
                    transform: translateY(0);
        }
    }

    @keyframes vibrate {
        0% {
            -webkit-transform: translate(0);
                    transform: translate(0);
        }
        20%,
        40%,
        60%,
        80% {
            -webkit-transform: translate(0.4rem, 0.2rem);
                    transform: translate(0.4rem, 0.2rem);
        }
        10%,
        30%,
        50%,
        90% {
            -webkit-transform: translate(-0.2rem, -0.1rem);
                    transform: translate(-0.2rem, -0.1rem);
        }
        70%,
        100% {
            -webkit-transform: translateY(0);
                    transform: translateY(0);
        }
    }
}

.btn {
    padding: 8px 20px;
    border-radius: 2px;
    border: none;
    cursor: pointer;
    outline: none;
    text-transform: uppercase;
    transition: all 0.4s ease-in-out;
    box-shadow: 6px 6px 6px var(--primary);
}

.btn--fill {
    background-color: var(--quaternary);
    color: var(--tertiary);
    border: 1px solid var(--quaternary);
}

.btn--outline {
    background-color: var(--tertiary);
    color: var(--quaternary);
    border: 1px solid var(--quaternary);
}

.btn--medium {
    padding: 8px 20px;
    font-size: 18px;
}

.btn--large {
    padding: 12px, 26px;
    font-size: 20px;
}

.btn:hover {
    background-color: var(--secondary);
    color: var(--quaternary);
    border-color: var(--secondary);
    /* box-shadow: 6px 6px 6px var(--primary); */
}


.cards-container {
    display: flex;
    flex-flow: column;
    align-items: center;
    align-content: center;
    justify-content: center;
    background: transparent;
    height: 100vh;
    width: 100%;
}

.cards-container h1 {
    text-align: center;
    color: var(--quaternary);
    font-size: 4rem;
    text-shadow: 6px 6px 6px var(--primary);
    margin-bottom: 200px;
}

.cards {
    list-style: none;
    position: relative;
    width: 80px;
    height: 80px;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    -webkit-animation: rotateG 30s linear infinite;
            animation: rotateG 30s linear infinite;
}

@-webkit-keyframes rotateG {
    from {
        -webkit-transform: perspective(350px) rotateY(0deg);
                transform: perspective(350px) rotateY(0deg);
    }
    to {
        -webkit-transform: perspective(350px) rotateY(360deg);
                transform: perspective(350px) rotateY(360deg);
    }
}

@keyframes rotateG {
    from {
        -webkit-transform: perspective(350px) rotateY(0deg);
                transform: perspective(350px) rotateY(0deg);
    }
    to {
        -webkit-transform: perspective(350px) rotateY(360deg);
                transform: perspective(350px) rotateY(360deg);
    }
}

.card {
    position: absolute;
    width: 100%;
    height: 100%;
    -webkit-transform-origin: center;
            transform-origin: center;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;   
    -webkit-transform: rotateY(calc(var(--i)*22.5deg)) translateZ(270px);   
            transform: rotateY(calc(var(--i)*22.5deg)) translateZ(270px);
}

.card figure img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 8px;
    object-fit: cover;
    transition: 2s;
}

video {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 2px;
    cursor: pointer;
    object-fit: contain;
}

.pop-up {
    position: fixed;
    width: 960px;
    height: 540px;
    border-radius: 2px;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%) scale(0);
            transform: translate(-50%, -50%) scale(0);
    background: transparent;
    z-index: 10;
    opacity: 0;
    transition: 0.5s ease-in-out;
    box-shadow: 0 0 20px 20px #0e081a50;
}

.pop-up.active {
    -webkit-transform: translate(-50%, -50%) scale(1);
            transform: translate(-50%, -50%) scale(1);
    opacity: 1;
}

.overlay {
    position: fixed;
    opacity: 0;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: var(--secondary);
    pointer-events: none;
    transition: 0.3s ease;
}

.overlay.active {
    pointer-events: all;
    opacity: 0.3;
}

.hero-container {
    background: transparent;
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-shadow: 6px 6px 6px var(--primary);
}

.hero-container > h1 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 100%;
    color: var(--quaternary);
    font-size: 8vw;
    text-align: center;
    text-shadow: 0.05em 0 0 var(--primary),
        -0.025em -0.05em 0 var(--secondary),
        0.05em 0.05em 0.05em var(--primary);
    -webkit-animation: glitch 0.5s infinite;
            animation: glitch 0.5s infinite;
}

.hero-container h1::after, .hero-container h1::before {
    display: none;
    content: 'RAIDEN SHOGUN';
    position: absolute;
    justify-self: center;
    align-self: center;
    opacity: .8;
}

.hero-container h1::before {
    -webkit-animation: glitch 0.65s infinite;
            animation: glitch 0.65s infinite;
    -webkit-clip-path: polygon(100% 70%, 100% 90%, 0 30%, 0 10%);
            clip-path: polygon(100% 70%, 100% 90%, 0 30%, 0 10%);
    -webkit-transform: translate(-0.025em, -0.025em);
            transform: translate(-0.025em, -0.025em);
}

.hero-container h1::after{
    -webkit-animation: glitch 0.35s infinite;
            animation: glitch 0.35s infinite;
    -webkit-clip-path: polygon(100% 10%, 100% 30%, 0 90%, 0 70%);
            clip-path: polygon(100% 10%, 100% 30%, 0 90%, 0 70%);
    -webkit-transform: translate(0.025em, 0.025em);
            transform: translate(0.025em, 0.025em);
}

@-webkit-keyframes glitch {
    0% {
        text-shadow: 0.05em 0 0 var(--primary),
            -0.025em -0.025em 0 var(--secondary),
            -0.05em 0.05em 0.05em var(--primary);
    }
    25% {
        text-shadow: -0.05em -0.025em 0 var(--primary),
            0.025em 0.025em 0 var(--secondary),
            -0.05em -0.05em 0.05em var(--primary);
    }
    50% {
        text-shadow: 0.025em 0.05em 0 var(--primary),
            0.05em 0 0 var(--secondary), 
            0 -0.05em 0.05em var(--primary);
    }
    75% {
        text-shadow: -0.025em 0 0 var(--primary),
            -0.025em -0.025em 0 var(--secondary),
            -0.05em 0.05em 0.05em var(--primary);
    }
    100% {
        text-shadow: 0.05em 0 0 var(--primary),
            -0.025em -0.05em 0 var(--secondary),
            0.05em 0.05em 0.05em var(--primary);
    }
}

@keyframes glitch {
    0% {
        text-shadow: 0.05em 0 0 var(--primary),
            -0.025em -0.025em 0 var(--secondary),
            -0.05em 0.05em 0.05em var(--primary);
    }
    25% {
        text-shadow: -0.05em -0.025em 0 var(--primary),
            0.025em 0.025em 0 var(--secondary),
            -0.05em -0.05em 0.05em var(--primary);
    }
    50% {
        text-shadow: 0.025em 0.05em 0 var(--primary),
            0.05em 0 0 var(--secondary), 
            0 -0.05em 0.05em var(--primary);
    }
    75% {
        text-shadow: -0.025em 0 0 var(--primary),
            -0.025em -0.025em 0 var(--secondary),
            -0.05em 0.05em 0.05em var(--primary);
    }
    100% {
        text-shadow: 0.05em 0 0 var(--primary),
            -0.025em -0.05em 0 var(--secondary),
            0.05em 0.05em 0.05em var(--primary);
    }
}

.hero-container > p {
    margin-top: 8px;
    color: var(--quaternary);
    font-size: 32px;
}

.hero-btns {
    margin-top: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.hero-btns .btn {
    margin: 6px;
    font-size: 25px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
}

.play-icon {
    margin-left: 10px;
}

@media screen and (max-width: 960px) {
    .pop-up.active {
        -webkit-transform: translate(-50%, -50%) scale(0.5);
                transform: translate(-50%, -50%) scale(0.5);
        box-shadow: 0 0 20px 15px #0e081a50;
    }

    .hero-container h1 {
        font-size: 60px;
    }

    .hero-container p {
        font-size: 28px;
    }
}

@media screen and (max-width: 768px) {
    .btn-mobile {
        display: block;
    }

    .btn {
        width: 100%;
    }

    .hero-btns {
        flex-direction: column;
    }
}

