@import url("https://fonts.googleapis.com/css2?family=Roboto+Slab&family=Rosarivo:ital@0;1&display=swap");

:root {
    --primary: #420824;
    --secondary: #721240;
    --tertiary: #0e081a;
    --quaternary: #ffeeec;
}

* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    font-family: "Rosarivo", serif;
}

#root {
    background-color: var(--tertiary);
    width: auto;
    height: auto;
}

body::-webkit-scrollbar {
    width: 0;
}

header {
    height: 80px;
    text-transform: uppercase;
    text-decoration: none;
    user-select: none;
}

main {
    min-height: 100vh;
    min-width: 100vw;
    user-select: none;
}

footer {
    height: 80px;
    user-select: none;
}

.particles {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.home {
    background: transparent;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 100%;
    width: 100%;
}
