video {
    position: absolute;
    width: 100%;
    height: 100%;
    border-radius: 2px;
    cursor: pointer;
    object-fit: contain;
}

.pop-up {
    position: fixed;
    width: 960px;
    height: 540px;
    border-radius: 2px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0);
    background: transparent;
    z-index: 10;
    opacity: 0;
    transition: 0.5s ease-in-out;
    box-shadow: 0 0 20px 20px #0e081a50;
}

.pop-up.active {
    transform: translate(-50%, -50%) scale(1);
    opacity: 1;
}

.overlay {
    position: fixed;
    opacity: 0;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    background: var(--secondary);
    pointer-events: none;
    transition: 0.3s ease;
}

.overlay.active {
    pointer-events: all;
    opacity: 0.3;
}

.hero-container {
    background: transparent;
    height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-shadow: 6px 6px 6px var(--primary);
}

.hero-container > h1 {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
    width: 100%;
    color: var(--quaternary);
    font-size: 8vw;
    text-align: center;
    text-shadow: 0.05em 0 0 var(--primary),
        -0.025em -0.05em 0 var(--secondary),
        0.05em 0.05em 0.05em var(--primary);
    animation: glitch 0.5s infinite;
}

.hero-container h1::after, .hero-container h1::before {
    display: none;
    content: 'RAIDEN SHOGUN';
    position: absolute;
    justify-self: center;
    align-self: center;
    opacity: .8;
}

.hero-container h1::before {
    animation: glitch 0.65s infinite;
    clip-path: polygon(100% 70%, 100% 90%, 0 30%, 0 10%);
    transform: translate(-0.025em, -0.025em);
}

.hero-container h1::after{
    animation: glitch 0.35s infinite;
    clip-path: polygon(100% 10%, 100% 30%, 0 90%, 0 70%);
    transform: translate(0.025em, 0.025em);
}

@keyframes glitch {
    0% {
        text-shadow: 0.05em 0 0 var(--primary),
            -0.025em -0.025em 0 var(--secondary),
            -0.05em 0.05em 0.05em var(--primary);
    }
    25% {
        text-shadow: -0.05em -0.025em 0 var(--primary),
            0.025em 0.025em 0 var(--secondary),
            -0.05em -0.05em 0.05em var(--primary);
    }
    50% {
        text-shadow: 0.025em 0.05em 0 var(--primary),
            0.05em 0 0 var(--secondary), 
            0 -0.05em 0.05em var(--primary);
    }
    75% {
        text-shadow: -0.025em 0 0 var(--primary),
            -0.025em -0.025em 0 var(--secondary),
            -0.05em 0.05em 0.05em var(--primary);
    }
    100% {
        text-shadow: 0.05em 0 0 var(--primary),
            -0.025em -0.05em 0 var(--secondary),
            0.05em 0.05em 0.05em var(--primary);
    }
}

.hero-container > p {
    margin-top: 8px;
    color: var(--quaternary);
    font-size: 32px;
}

.hero-btns {
    margin-top: 32px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.hero-btns .btn {
    margin: 6px;
    font-size: 25px;
    display: inline-flex;
    justify-content: center;
    align-items: center;
}

.play-icon {
    margin-left: 10px;
}

@media screen and (max-width: 960px) {
    .pop-up.active {
        transform: translate(-50%, -50%) scale(0.5);
        box-shadow: 0 0 20px 15px #0e081a50;
    }

    .hero-container h1 {
        font-size: 60px;
    }

    .hero-container p {
        font-size: 28px;
    }
}

@media screen and (max-width: 768px) {
    .btn-mobile {
        display: block;
    }

    .btn {
        width: 100%;
    }

    .hero-btns {
        flex-direction: column;
    }
}
