.cards-container {
    display: flex;
    flex-flow: column;
    align-items: center;
    align-content: center;
    justify-content: center;
    background: transparent;
    height: 100vh;
    width: 100%;
}

.cards-container h1 {
    text-align: center;
    color: var(--quaternary);
    font-size: 4rem;
    text-shadow: 6px 6px 6px var(--primary);
    margin-bottom: 200px;
}

.cards {
    list-style: none;
    position: relative;
    width: 80px;
    height: 80px;
    transform-style: preserve-3d;
    animation: rotateG 30s linear infinite;
}

@keyframes rotateG {
    from {
        transform: perspective(350px) rotateY(0deg);
    }
    to {
        transform: perspective(350px) rotateY(360deg);
    }
}

.card {
    position: absolute;
    width: 100%;
    height: 100%;
    transform-origin: center;
    transform-style: preserve-3d;   
    transform: rotateY(calc(var(--i)*22.5deg)) translateZ(270px);
}

.card figure img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 8px;
    object-fit: cover;
    transition: 2s;
}
