nav {
    position: fixed;
    top: 0;
    z-index: 9;
    height: 80px;
    width: 100%;
}

.navbar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: var(--quaternary);
}

.navbar-container {
    display: flex;
    align-items: center;
    justify-self: flex-start;
    height: 100%;
    max-width: 1500px;
    margin: 0 20px;
}

.navbar-logo {
    display: flex;
    align-items: center;
    justify-self: center;
    text-decoration: none;
    margin: 0 10px;
    cursor: pointer;
    color: var(--tertiary);
}

.electro {
    width: 70px;
}

.eternity {
    width: 16rem;
    height: 60px;
}

.menu-icon {
    display: none;
}

.nav-menu {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin: 0 20px;
    font-size: 1rem;
}

.nav-menu button {
    color: var(--tertiary);
    border-color: var(--tertiary);
    background-color: var(--quaternary);
}

.nav-list {
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-gap: 20px;
    list-style: none;
    text-align: center;
}

.nav-item {
    position: relative;
    display: flex;
    width: 100px;
    height: 40px;
}

.nav-item-mobile {
    display: none;
}

.nav-link {
    color: var(--tertiary);
    width: 100%;
    height: 100%;
    padding: 10px 20px;
    position: relative;
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    overflow: hidden;
    transition: .4s ease-in-out;
}

.nav-link::before,
.nav-link::after {
    content: "";
    position: absolute;
    box-sizing: border-box;
    transition: 0.4s ease-in-out;
    z-index: -1;
}

.nav-link::before {
    background: var(--secondary);
    width: 125%;
    height: 50%;
    animation: menuItems 4s linear infinite;
}

.nav-link::after {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    transform: scale(0.95, 0.9);
    background: var(--quaternary);
}

@keyframes menuItems {
    to {
        transform: rotate(360deg);
    }
}

.nav-link:hover::before {
    height: 300%;
    width: 300%;
}

.nav-link:hover::after {
    background: var(--secondary);
}

.nav-link:hover {
    box-shadow: 6px 6px 6px var(--primary);
    color: var(--quaternary);
}

@media screen and (max-width: 960px) {
    .navbar-container {
        display: flex;
        justify-content: space-between;
        width: 100%;
    }

    .menu-icon {
        display: flex;
        cursor: pointer;
        transition: .6s ease-out;
    }

    .menu-icon svg {
        stroke: var(--tertiary);
        width: 40px;
        height: 40px;
    }

    .menu-icon.active svg {
        stroke: var(--secondary);
    }

    .nav-menu .btn {
        display: none;
    }

    .nav-item,
    .nav-item-mobile {
        position: relative;
        display: flex;
        width: auto;
        height: 50px;
    }

    .nav-list {
        margin: 0;
        padding: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 20%;
        flex-direction: column;
    }

    .nav-menu {
        width: 100%;
        height: 90vh;
        margin: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: 80px;
        right: -100%;
        opacity: 0;
        transition: all 0.5s ease;
        background: repeating-linear-gradient(45deg, var(--secondary), var(--primary));
    }

    .nav-menu.active {
        right: 0;
        opacity: 1;
        transition: all 0.5s ease;
        z-index: 1;
    }

    .nav-link {
        text-align: center;
        align-items: center;
        justify-content: flex-start;
        padding: 16px;
        width: 100%;
        display: flex;
        transition: all 0.4s ease;
        background: var(--quaternary);
        box-shadow: 6px 6px 6px var(--tertiary);
    }

    .nav-link::after,
    .nav-link::before {
        display: none;
    }

    .nav-link:hover {
        background-color: var(--tertiary);
        color: var(--quaternary);
        transform: scale(1.2);
        animation: vibrate 0.5s ease-in;
        box-shadow: 6px 6px 6px var(--tertiary);
    }

    @keyframes vibrate {
        0% {
            transform: translate(0);
        }
        20%,
        40%,
        60%,
        80% {
            transform: translate(0.4rem, 0.2rem);
        }
        10%,
        30%,
        50%,
        90% {
            transform: translate(-0.2rem, -0.1rem);
        }
        70%,
        100% {
            transform: translateY(0);
        }
    }
}
