.eternity {
    fill: transparent;
    animation: fill-line 0.5s ease-in-out forwards 2s;
}

.eternity path {
    stroke: var(--tertiary);
    stroke-width: 0.4px;
    animation: line-anim 1.5s ease-in-out forwards;
}

.eternity path:nth-child(1) {
    stroke-dasharray: 60;
    stroke-dashoffset: 60;
    animation-delay: .1s;
}
.eternity path:nth-child(2) {
    stroke-dasharray: 45;
    stroke-dashoffset: 45;
    animation-delay: .2s;
}
.eternity path:nth-child(3) {
    stroke-dasharray: 56;
    stroke-dashoffset: 56;
    animation-delay: .3s;
}
.eternity path:nth-child(4) {
    stroke-dasharray: 57;
    stroke-dashoffset: 57;
    animation-delay: .4s;
}
.eternity path:nth-child(5) {
    stroke-dasharray: 65;
    stroke-dashoffset: 65;
    animation-delay: .5s;
}
.eternity path:nth-child(6) {
    stroke-dasharray: 30;
    stroke-dashoffset: 30;
    animation-delay: .6s;
}
.eternity path:nth-child(7) {
    stroke-dasharray: 45;
    stroke-dashoffset: 45;
    animation-delay: .7s;
}
.eternity path:nth-child(8) {
    stroke-dasharray: 45;
    stroke-dashoffset: 45;
    animation-delay: .8s;
}

@keyframes line-anim {
    to {
        stroke-dashoffset: 0;
    }
}

@keyframes fill-line {
    to {
        fill: var(--tertiary);
        filter: drop-shadow(8px 8px 8px var(--primary));
    }
}

.electro {
    fill: none;
    transition: 3s ease;
    animation: rotateSvg 8s linear infinite;
}

.electro path {
    fill: var(--tertiary);
    filter: drop-shadow(0 0 3px var(--primary));
}

.electro circle {
    fill: var(--secondary);
    filter: blur(8px);
}

@keyframes rotateSvg {
    to {
        transform: rotate(2turn);
    }
}